import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  public palavra: string;
  public list: any = "";
  public i = 1;

  constructor(private http: Http, public navCtrl: NavController) {


  }
  busca(value) {
    let send = { palavra: value, page: this.i };
    console.log('chamou')
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    headers.append('x-acess-token', 'eyJhbGciOiJIUzUxMiJ9.YWRtaW4.VOGP2A6Zq82bvXR14D3qMGoFiyDET7I_RTIRrnPgHBG0K0sMvVk24y0W8bB9tDG0dTpXoPhq669pV7b4rSdORg');

    let options = new RequestOptions({ headers: headers });

    this.http.post('http://localhost:8080/user', send, options)
      .map(res => res.json())
      .subscribe((response) => {
        console.log(response)
        this.list = response.newUsers;
      }, (error) => {
        console.log(error)
      })


  }

  retorno() {
    if (this.list.length < 15)
      return false
    else return true
  }
  buscaSeq(value, i) {

    if (i == 0) {
      this.i--;
      if (this.i < 0) {
        this.i = 1;
      }
    } else {
      this.i++;
    }

    let send = { palavra: value, page: this.i };
    console.log('chamou')
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    headers.append('x-acess-token', 'eyJhbGciOiJIUzUxMiJ9.YWRtaW4.VOGP2A6Zq82bvXR14D3qMGoFiyDET7I_RTIRrnPgHBG0K0sMvVk24y0W8bB9tDG0dTpXoPhq669pV7b4rSdORg');

    let options = new RequestOptions({ headers: headers });

    this.http.post('http://localhost:8080/user', send, options)
      .map(res => res.json())
      .subscribe((response) => {
        console.log(response)
        this.list = response.newUsers;
      }, (error) => {
        console.log(error)
      })


  }
}



